'use strict';

/**
* Transform stream which joins streamed data.
*
* @module @stdlib/streams-node-join
*
* @example
* import stdout from '@stdlib/streams-node-stdout';
* import joinStream from '@stdlib/streams-node-join';
*
* var stream = joinStream();
*
* stream.pipe( stdout );
*
* stream.write( '1' );
* stream.write( '2' );
* stream.write( '3' );
*
* stream.end();
*
* // prints: '1\n2\n3'
*
* @example
* import joinStream from '@stdlib/streams-node-join';
*
* var opts = {
*     'sep': '\t',
*     'objectMode': true,
*     'encoding': 'utf8',
*     'highWaterMark': 64
* };
*
* var factory = joinStream.factory( opts );
*
* // Create 10 identically configured streams...
* var streams = [];
* var i;
* for ( i = 0; i < 10; i++ ) {
*     streams.push( factory() );
* }
*
* @example
* import stdout from '@stdlib/streams-node-stdout';
* import joinStream from '@stdlib/streams-node-join';
*
* var stream = joinStream.objectMode({
*     'sep': ','
* });
*
* stream.pipe( stdout );
*
* stream.write( 'a' );
* stream.write( 'b' );
* stream.write( 'c' );
*
* stream.end();
*
* // prints: 'a,b,c'
*/


// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import objectMode from './object_mode.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'objectMode', objectMode );
setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
